console.log('background script loaded');

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
  if (message.type === "PRODUCT_DATA") {
    console.log("Background received product data:", message.payload);
    // You can process or save the data here
    
    sendResponse({ received: true }); // optional acknowledgment
  }
  // If you want to send response asynchronously, return true, else false
  return false;
});
